*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  @media (max-width: 30rem) {
    font-size: 30%;
  }

  @media (max-width: 50rem) {
    font-size: 50%;
  }
}

body {
  font-family: "Courier New", Courier, monospace;
  font-size: 1rem;
  box-sizing: border-box;
}

ul,
ol,
li {
  list-style: none;
  text-decoration: none;
  font-size: 3rem;
}

a,
a:active,
a:hover {
  text-decoration: none;
}

h1,
h1:active {
  font-size: 8rem;
}

h2,
h2:active {
  font-size: 5rem;
}

